import authorize from '@/components/authorize'
import utils from '@/utils/utils.js'
var app = getApp();
export default {
	components: {
		authorize
	},
	data() {
		return {
			info: [{
					imgurl: '../../static/images/room/20181214161550000808031.jpg'
				},
				{
					imgurl: '../../static/images/room/20181214172226515808034.jpg'
				},
				{
					imgurl: '../../static/images/room/20181214175519996808036.jpg'
				}
			],
			globalData: {
				code: '',
				userInfo: null,
				today: '',
				checkIn: '',
				checkOut: ''
			},
			current: 0,
			mode: 'long',
			count: 10
		};
	},
	onShow() {
		let that = this
		that.globalData = app.$vm.globalData
		console.log(this.globalData)
		utils.checkSession().then(res => {
			// that.goLogin();
		}).catch(res => {
			that.isAuthorizeShow = true
		});
	},
	methods: {
		change(e) {
			this.current = e.detail.current;
		},
		gotoRoomInfo() {
			uni.navigateTo({
				url: '../roominfo/roominfo',
				animationType: 'fade-in',
				animationDuration: 1000
			});
		},
		selectDate() {
			uni.navigateTo({
				url: '../selectDate/selectDate?checkIn='+JSON.stringify(this.globalData.checkIn)+'&checkOut='+JSON.stringify(this.globalData.checkOut),
				animationType: 'fade-in',
				animationDuration: 1000
			});
		},
		// 获取个人信息
		getUserInfo(res) {
			console.log(res)
			this.globalData.userInfo = res.userInfo;

			api.authorize({
				appid: globalData.appid,
				code: globalData.code,
				encryptedData: res.encryptedData,
				iv: res.iv
			}).then((res) => {
				console.log(res)
			});

		},
	}
};
